






import { Component, Prop, Vue } from 'vue-property-decorator';

@Component({})
export default class BaseButton extends Vue {
  @Prop({
    type: Function,
  })
  readonly onClick!: (a: Event) => Promise<void> | any;

  click(event: Event) {
    if (typeof this.onClick === 'function') {
      return this.onClick(event);
    }
    return null;
  }
}
